@if $_use_br == true {

	.-ol-br {

		&._mb-none {
			display: inline;

			@include md-mobile {
				display: none;
			}

			+ ._br-indemn {
				display: none;

				@include md-mobile {
					display: inline;
				}
			}
		}

		&._mb-only {
			display: none;

			@include md-mobile {
				display: inline;
			}

			+ ._br-indemn {
				display: inline;

				@include md-mobile {
					display: none;
				}
			}
		}
	}
}