@mixin btn-variant( $c-btn,
					$c-btn_hover,
					$per-btn_hover: $per-btn_hover,
					$brd_c-btn: $c-btn ) {

	border-color: $brd_c-btn;
	background-color: transparent;
	color: $c-btn;

	&:hover {
		border-color: $brd_c-btn;
		background-color: $c-btn;
		color: $c-btn_hover;
	}

	&:active {
		border-color: lighten($brd_c-btn, $per-btn_active);
		background-color: lighten($c-btn, $per-btn_active);
	}
}



@mixin btn-default-variant( $c-btn,
							$bg-btn,
							$per-btn_hover: $per-btn_hover,
							$per-btn_active: $per-btn_active,
							$brd_c-btn: $c-btn ) {

	border-color: $brd_c-btn;
	background-color: $bg-btn;
	color: $c-btn;

	&:hover {
		border-color: lighten($brd_c-btn, $per-btn_hover);
		background-color: lighten($bg-btn, $per-btn_hover);
		color: $c-btn;
	}

	&:active {
		border-color: lighten($brd_c-btn, $per-btn_hover + $per-btn_active);
		background-color: lighten($bg-btn, $per-btn_hover + $per-btn_active);
	}
}



@mixin btn-link( $brd-b-c-btn,
				 $c-btn,
				 $brd-b-c-btn_hover,
				 $c-btn_hover ) {

	border-color: transparent;
	border-bottom: 1px solid $brd-b-c-btn;
	color: $c-btn;

	&:hover {
		border-bottom-color: $brd-b-c-btn_hover;
		color: $c-btn_hover;
	}
}



@mixin btn-filled-variant( $c-btn,
						   $bg-btn,
						   $per-brd,
						   $per-btn_hover: $per-btn_hover,
						   $per-btn_active: $per-btn_active,
						   $brd_c-btn: darken($bg-btn, $per-brd) ) {

	border-color: $brd_c-btn;
	background-color: $bg-btn;
	color: $c-btn;

	&:hover {
		border-color: lighten($brd_c-btn, $per-btn_hover);
		background-color: lighten($bg-btn, $per-btn_hover);
		color: $c-btn;
	}

	&:active {
		border-color: darken($brd_c-btn, $per-btn_active);
		background-color: darken($bg-btn, $per-btn_active);
		color: $c-btn;
	}
}



// Button sizes
@mixin btn-size( $w-btn,
				 $h-btn,
				 $brd_rd-btn,
				 $p_vert-btn,
				 $p_horiz-btn,
				 $f_sz-btn,
				 $lh-btn,
				 $brd_w-btn ) {

	width: $w-btn;
	height: $h-btn;

	border-radius: $brd_rd-btn;
	border-width: $brd_w-btn;
	padding: $p_vert-btn $p_horiz-btn;

	font-size: $f_sz-btn;
	line-height: $lh-btn;
}