@if $_use_forms == true {

	.-ol-input,
	.-ol-textarea,
	.jq-selectbox {
		display: block;
		width: 100%;
	}

	.-ol-input,
	.-ol-textarea {
		padding: $p_vert-input $p_horiz-input;
		border: $brd-input solid $brd_c-input;
		border-radius: $brd_rd-input;

		background: $bg-input;

		color: $c-input;
		font-size: $fsz-input;
		line-height: $h-input - $brd-input * 2 - $p_vert-input * 2;

		@include placeholder($placeholder, transparent, $placeholder);
	}

	.-ol-input {
		height: $h-input;
		text-overflow: ellipsis;

		&-wrap {
			width: 100%;
			position: relative;

			&._error {

				.-ol-input,
				.jq-selectbox {
					border-color: $brd_c-form-error;
					color: $c-form-error;
				}

				.jq-selectbox {

					&__select {
						border-color: $brd_c-form-error;
					}

					&__trigger {

						&:before {
							background-color: $brd_c-form-error;
						}
					}
				}
			}
		}

		&-file {
			position: relative;
			overflow: hidden;
			width: 100%;

			input {
				$h-input-file: 100%;

				width: 100%;
				height: $h-input-file;

				position: absolute;
				top: 0;
				left: 0;
				z-index: 3;

				opacity: 0;

				cursor: pointer;
			}
		}
	}

	.-ol-textarea {
		height: $h-textarea;
		resize: none;
		overflow: auto;
	}

	.-ol-label-wrap {
		display: block; // for label block

		.-ol-label {
			color: $c-label;
			font-size: $f_sz-label;
			font-weight: $f_w-label;

			& + .-ol-input,
			& + .-ol-textarea,
			& + .jq-selectbox {
				margin-top: $mb-label;
			}
		}
	}

	.-ol-row-input {
		width: 100%;

		@include inline('.-ol-input-item');

		.-ol-input-item {
			width: $per-input_col-many;
			position: relative;

			&._lg {
				width: 100%;
			}

			+ .-ol-input-item {
				margin-left: $m-col-input;
			}
		}

		& + & {
			margin-top: $m-row-input;
		}
	}

	.-ol-form-body {
		margin-bottom: $m-form-body;
	}
}