@if $_use_modifiers == true {

	._mb-none {
		display: block !important;

		@include md-mobile {
			display: none !important;
		}

		&._inline {
			display: inline !important;

			@include md-mobile {
				display: none !important;
			}
		}
	}

	._mb-only {
		display: none !important;

		@include md-mobile {
			display: block !important;
		}

		&._inline {

			@include md-mobile {
				display: inline !important;
			}
		}
	}
}