@mixin md-tablet {

	@media only screen
	and (max-width: $md-tablet) {
		@content;
	}
}

@mixin md-mobile {

	@media only screen
	and (max-width: $md-mobile) {
		@content;
	}
}

@mixin md-desk_min {

	@media only screen
	and (max-width: $md-desk_min) {
		@content;
	}
}

@mixin md-ie_10-plus {

	@media screen
	and (-ms-high-contrast: active),
		(-ms-high-contrast: none) {
		@content;
	}
}

@mixin md-ie_9-plus {

	@media screen
	and (min-width: #{0\0}) {
		@content;
	}
}