@mixin e-headings {

	h1, .-ol-h1,
	h2, .-ol-h2,
	h3, .-ol-h3,
	h4, .-ol-h4,
	h5, .-ol-h5,
	h6, .-ol-h6 {
		@content;
	}
}

@mixin e-h1 {

	h1, .-ol-h1 {
		@content;
	}
}

@mixin e-h2 {

	h2, .-ol-h2 {
		@content;
	}
}

@mixin e-h3 {

	h3, .-ol-h3 {
		@content;
	}
}

@mixin e-h4 {

	h4, .-ol-h4 {
		@content;
	}
}

@mixin e-h5 {

	h5, .-ol-h5 {
		@content;
	}
}

@mixin e-h6 {

	h6, .-ol-h6 {
		@content;
	}
}

@mixin e-p {

	p, .-ol-p {
		@content;
	}
}

@mixin e-b {

	b, .-ol-b {
		@content;
	}
}

@mixin e-btns {

	.-ol-btn,
	.-ol-btn-link,
	.-ol-btn-outline,
	.-ol-btn-default,
	.-ol-btn-primary,
	.-ol-btn-success,
	.-ol-btn-info,
	.-ol-btn-warning,
	.-ol-btn-danger {
		@content;
	}
}